.App {
  min-height: 100vh;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.play-store-btn {
  cursor: "pointer";
  width: fit-content;
}
.head-container {
  background: rgb(202, 252, 255);
  background: linear-gradient(
    180deg,
    rgba(202, 252, 255, 1) 0%,
    rgba(9, 121, 116, 0) 81%,
    rgba(255, 255, 255, 1) 100%
  );
}
